/* Global */
.root {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
/* Header */
.top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(0,0,0,.8);
  width: 100vw;
  min-height: 5em;
  text-align: center;
}
.top-sub {
  padding: .5em;
  margin: .5em;
  color: rgba(255,255,255,0.87);
}
.top-sub.left {
}
.top-sub.center {
}
.top-sub.right {
}
/* Pages Global */
.sector {
  display: inherit;
  justify-content: inherit;
  align-items: inherit;
  width: 100vw;
  height: 100vh;
}

/* Page One */
.sector.one {
  width: 100vw;
  height: calc(100vh - 5em);
  background-image: linear-gradient(to right, #4FACFE 0%, #00F2FE 100%);  
}
.sector.one.layer{

}
/* Page Two */
.sector.two {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  background-image: url('/images/catch.jpg');
  background-size: cover;
  color: black;
}
.two.text {
  margin: 0.5em;
  padding: 0.5em;
}
.title {
  font-size: calc(18px + 2vmin);
}
/* Page Three */
.sector.three {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(255,255,0,.2);
}
.three.text {
  margin: 0.5em;
  padding: 0.5em;
  background-color: rgba(255,255,255,.1);
}
/* Page Four */
.sector.four {
  background-color: rgba(0,255,255,.2);
}
/* Page Five */
.sector.five {
  background-color: rgba(255,0,255,.2);
}